import { Supplier } from './SupplierCards';
interface Props {
  supplier: Supplier;
  onClose: () => void;
  onEdit: () => void;
}
export default function SupplierDetailsModal({ supplier, onClose, onEdit }: Props) {
  return (
    <div className="fixed inset-0 bg-black bg-opacity-30 z-50 flex justify-center items-center">
      <div className="bg-white w-full max-w-md rounded-md shadow-md relative">
        <div className="flex justify-between items-center p-4 pb-2">
          <h2 className="text-lg font-semibold">{supplier.name}</h2>
          <button
            onClick={onClose}
            className="text-gray-500 text-xl hover:text-gray-700"
          >
            ×
          </button>
        </div>
        <div className="px-4 text-sm text-gray-500">
          <p>Contact: {supplier.contact_name}</p>
        </div>
        
        <div className="grid grid-cols-2 gap-4 p-4 text-sm border-b">
          <div>
            <h4 className="font-medium text-gray-500 mb-2">Contact Information</h4>
            <div className="flex items-center gap-2 mb-2">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
              </svg>
              <p>{supplier.phone || "555-123-4567"}</p>
            </div>
            <div className="flex items-center gap-2">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
              </svg>
              <p>{supplier.email || "contact@techdist.com"}</p>
            </div>
          </div>
          <div>
            <h4 className="font-medium text-gray-500 mb-2">Address</h4>
            <p>{supplier.address || "123 Tech Blvd"}</p>
            <p>{supplier.city || "San Francisco"}, {supplier.state || "CA"} {supplier.postal_code || "94107"}</p>
            <p>{supplier.country || "United States"}</p>
          </div>
        </div>
        
        <div className="p-4 text-sm border-b">
          <h4 className="font-medium text-gray-500 mb-2">Notes</h4>
          <p className="text-gray-600">{supplier.notes || "Primary electronics supplier"}</p>
        </div>
        
        <div className="flex justify-between p-4 gap-2">
          <button
            onClick={onEdit}
            className="border border-gray-300 px-4 py-2 rounded text-sm hover:bg-gray-50"
          >
            Edit Supplier
          </button>
          <button
            onClick={() =>
              window.location.assign(`/admin/suppliers/${supplier.id}/products`)
            }
            className="bg-blue-500 text-white px-4 py-2 rounded text-sm hover:bg-blue-600"
          >
            View Products
          </button>
        </div>
      </div>
    </div>
  );
}